.highlightsContainer {
  background: #fff;
  min-height: 80vh;
  padding: 20px 0;
  font-family: 'ACCEZZFONT';
}

.header {
  text-align: center;
  margin-bottom: 30px;
  padding: 0 20px;
}

.title {
  font-size: 2.5rem;
  color: #000;
  margin-bottom: 10px;
  font-weight: 700;
}

.subtitle {
  font-size: 1.1rem;
  color: #000;
  font-weight: 600;
  margin: 0 auto;
  max-width: 1200px;
}

.carouselContainer {
  max-width: 1314px;
  margin: 0 auto;
  padding: 0 20px;
}

/* 轮播组件样式 */
.carouselWrapper {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.carouselTrack {
  display: flex;
  transition: transform 0.5s ease-in-out;
  width: 100%;
}

.carouselSlide {
  flex: 0 0 100%;
  width: 100%;
}

.slideContent {
  display: flex;
  align-items: center;
  min-height: 400px;
  background: #fff;
}

.slideImage {
  flex: 0 0 50%;
  height: 600px;
  overflow: hidden;
}

.slideImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.slideImage:hover img {
  transform: scale(1.05);
}

.slideText {
  flex: 1;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.slideTitle {
  font-size: 2rem;
  font-weight: 700;
  color: #000;
  margin-bottom: 20px;
  line-height: 1.2;
}

.slideDescription {
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
  margin: 0;
  max-height: 400px;
  overflow: auto;
  font-weight: 600;
}

/* 导航圆圈 */
.carouselDots {
  display: flex;
  justify-content: center;
  gap: 12px;
  position: absolute;
  bottom: 30px;
  left: 48%;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid #ddd;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.dot:hover {
  border-color: #999;
  background: #f0f0f0;
}

.activeDot {
  border-color: #000;
  background: #000;
}

/* 左右导航按钮 */
.carouselNav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 48px;
  height: 48px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  z-index: 10;
}

.carouselNav:hover {
  background: rgba(255, 255, 255, 1);
  transform: translateY(-50%) scale(1.1);
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.15);
}

.prevNav {
  left: 20px;
}

.nextNav {
  right: 20px;
}

.carouselNav svg {
  width: 20px;
  height: 20px;
  color: #000;
}
/* 移动端适配 */
@media (max-width: 1024px) {
  .propertyCard {
    flex: 0 0 180px;
  }

  .imageContainer {
    height: 180px;
  }

  .secondRow {
    transform: translateX(calc(50% + 7.5px));
  }

  .navigationButton {
    width: 45px;
    height: 45px;
  }

  .prevButton {
    left: -22px;
  }

  .nextButton {
    right: -22px;
  }
}

@media (max-width: 768px) {
  .highlightsContainer {
    padding: 15px 0;
  }

  .header {
    margin-bottom: 25px;
    padding: 0 15px;
  }

  .title {
    font-size: 2rem;
  }

  .subtitle {
    font-size: 1rem;
  }

  .carouselContainer {
    padding: 0 15px;
  }

  .slideContent {
    flex-direction: column;
    min-height: auto;
  }
  
  .slideImage {
    flex: 0 0 100%;
    height: 250px;
  }
  
  .slideText {
    padding: 40px 30px;
    text-align: center;
  }
  
  .slideTitle {
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  .slideDescription {
    font-size: 0.9rem;
  }
  
  .carouselNav {
    width: 40px;
    height: 40px;
  }
  
  .prevNav {
    left: 15px;
  }
  
  .nextNav {
    right: 15px;
  }
  
  .carouselDots {
    margin-top: 20px;
    padding: 15px 0;
    left: 40%;
  }
}

@media (max-width: 480px) {
  .slideImage {
    height: 200px;
  }
  
  .slideText {
    padding: 30px 20px;
  }
  
  .slideTitle {
    font-size: 1.5rem;
  }
  
  .slideDescription {
    font-size: 0.85rem;
  }
  
  .carouselNav {
    width: 36px;
    height: 36px;
  }
  
  .prevNav {
    left: 10px;
  }
  
  .nextNav {
    right: 10px;
  }.slideImage {
    height: 200px;
  }
  
  .slideText {
    padding: 30px 20px;
  }
  
  .slideTitle {
    font-size: 1.8rem;
  }
  
  .slideDescription {
    font-size: 0.95rem;
  }
  
  .carouselNav {
    width: 36px;
    height: 36px;
  }
  
  .prevNav {
    left: 10px;
  }
  
  .nextNav {
    right: 10px;
  }
}